:root {
  --igx-ripple-variant: material;
  --igx-ripple-color: rgba(0, 0, 0, 0.74);
}

.igx-ripple__inner {
  display: block;
  position: absolute;
  border-radius: 50%;
  background: var(--igx-ripple-color);
  pointer-events: none;
  transform-origin: center;
  transform: translate3d(0, 0, 0) scale(0);
  will-change: opacity, transform;
  opacity: 0.5;
  margin: 0 !important;
  border: none !important;
}

.igx-ripple {
  overflow: hidden;
}

:root {
  --igx-avatar-border-radius-round: 44px;
  --igx-avatar-border-radius-square: 0px;
  --igx-avatar-icon-background: rgba(0, 0, 0, 0.26);
  --igx-avatar-icon-color: rgba(0, 0, 0, 0.74);
  --igx-avatar-initials-background: rgba(0, 0, 0, 0.26);
  --igx-avatar-initials-color: rgba(0, 0, 0, 0.74);
  --igx-avatar-image-background: transparent;
}

.igx-avatar, .igx-avatar--rounded {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  color: var(--igx-avatar-initials-color);
  background: var(--igx-avatar-initials-background);
  vertical-align: middle;
  border-radius: var(--igx-avatar-border-radius-square);
  outline-style: none;
}
.igx-avatar::after, .igx-avatar--rounded::after {
  box-shadow: none;
  transition: box-shadow 0.15s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.igx-avatar__image {
  width: 100%;
  height: 100%;
  border-radius: inherit;
}

.igx-avatar__image {
  background-color: var(--igx-avatar-image-background);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.igx-avatar--rounded {
  border-radius: var(--igx-avatar-border-radius-round);
}

.igx-avatar--small {
  width: 2.5rem;
  height: 2.5rem;
  min-width: 2.5rem;
}

.igx-avatar--medium {
  width: 4rem;
  height: 4rem;
  min-width: 4rem;
}

.igx-avatar--large {
  width: 5.5rem;
  height: 5.5rem;
  min-width: 5.5rem;
}

.igx-avatar--icon {
  color: var(--igx-avatar-icon-color);
  background: var(--igx-avatar-icon-background);
}
.igx-avatar--initials {
  text-transform: uppercase;
  color: var(--igx-avatar-initials-color);
  background-color: var(--igx-avatar-initials-background);
}
.igx-avatar--initials {
  font-size: 1.25rem;
  line-height: 2.5rem;
}

.igx-avatar--medium.igx-avatar--initials {
  font-size: 2rem;
  line-height: 4rem;
}

.igx-avatar--large.igx-avatar--initials {
  font-size: 2.75rem;
  line-height: 5.5rem;
