/* autoprefixer grid: on */
html, body, app-root {
  height: 100%;
}

body {
  overflow-y: auto;
  overflow-x: hidden;
}

@media only screen and (max-width: 786px) {
  body {
    overflow-x: auto;
  }
}
.content p {
  margin: 15px;
}

app-home {
  width: 100%;
}

app-block-grid {
  width: 100%;
}

app-portfolio {
  width: 100%;
}

app-portfolio {
  width: 100%;
}

app-statistics {
  width: 100%;
}

.sample-wrapper {
  width: inherit;
  position: relative;
  height: 100%;
  padding: 20px;
  background: transparent;
}

#lockIcon {
  width: 9.5rem;
  height: 9.5rem;
  font-size: 9.5rem;
  color: #E29C45;
  margin: 0.2em auto 0 auto;
  display: block;
}
@media only screen and (max-width: 786px) {
  #lockIcon {
    width: 50px;
    height: 50px;
    font-size: 50px;
  }
}

.sample-content {
  display: flex;
  flex-grow: 1;
  height: 100%;
  flex-flow: row wrap;
  justify-content: flex-start;
  padding: 0;
  -webkit-animation: fade-in 0.3s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  -moz-animation: fade-in 0.3s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  animation: fade-in 0.3s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

.sample-column {
  display: flex;
  flex-flow: column nowrap;
  flex: 1 1 auto;
  align-content: flex-start;
  margin: 16px;
  min-width: 240px;
}

.sample-title {
  color: #0375be;
  margin-bottom: 24px;
}

.sample-title .light {
  font-weight: 400;
}

.sample-description {
  font-size: 14px;
  font-weight: normal;
  color: #717171;
}

.igx-card {
  flex-flow: row;
}

.igx-card igx-card-content {
  padding: 0;
}

.form-container {
  height: calc(100vh - 56px);
  display: flex;
  align-items: center;
}

igx-grid .percent-style-up,
igx-grid .percent-style-down {
  position: relative;
  padding-right: 10px;
}
igx-grid .percent-style-up:after,
igx-grid .percent-style-down:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 4px;
  border-radius: 5px;
}

.percent-style-up {
  color: #3f9a47;
}
.percent-style-up:after {
  background: #3f9a47;
}

.percent-style-down {
  color: #aa2222;
}
.percent-style-down:after {
  background: #aa2222;
}

.igx-grid__td--selected.negative {
  background: #aa2222 !important;
}
.igx-grid__td--selected.negative .percent-style-down {
  color: #fff !important;
}
.igx-grid__td--selected.negative .daily-change-negative {
